ul {
  /* display: flex; */
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button:hover {
  opacity: 0.7;
  cursor: pointer;
}

.toolbarSection {
  padding: 0.5vh 0.5vw;
  border-left: 0.15vw solid #06d1d1;
  height: 92.5%
}

.loader {
    border-right: 1.5vw solid #39cc00; /* Green */
    border-left: 1.5vw solid #39cc00; /* Green */
    border-bottom: 1.5vw solid #a7bde2; /* Blue */
    border-top: 1.5vw solid #a7bde2; /* Blue */
    border-radius: 50%;
    width: 4vw;
    height: 4vw;
    -webkit-animation: spin 2.5s linear infinite;
            animation: spin 2.5s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

div[tabindex="-1"]:focus {
    outline: 0;
}

.tableRow{
  border-bottom: 0.1vw solid rgba(234, 234, 234, 0.15);
  background-color: rgba(0, 0, 0, 0);
}

.tableRow:hover{
  background-color: rgba(6, 209, 209, 0.2);
  -webkit-transition: background-color 0.25s ease-in-out;
  -o-transition: background-color 0.25s ease-in-out;
  transition: background-color 0.25s ease-in-out;
}

.imgButton{
  outline: 0.1vw rgba(160, 160, 160, 0) solid;
  background-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 0
}

.imgButton:hover{
  opacity: 0.9;
  /* outline: 0.1vw rgba(50, 50, 50, 0.6) solid;
  background-color: rgba(160, 160, 160, 0.5);
  transition: all 0.25s ease-in-out; */
  cursor: pointer;
}

/* width */
::-webkit-scrollbar {
    width: 0.6vw;
    height: 0.6vw;
    padding: 0;
}

/* Track */
::-webkit-scrollbar-track {
    background: #c4c4c4;
    border-radius: 2vw;
    -webkit-box-shadow: inset 2px 2px 6px rgba(0,0,0,1);
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 2vw;
    background-color: #0c0c0c;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background-color: #000;
}



